<script>
import { downloadPDF } from "@/utils/pdf.js";

const map = [
  "Protein in g",
  "Vitamin A retinol equivalents in ug",
  "Vitamin C in mg",
  "Calcium (Ca) in mg",
  "Folate natural in ug",
  "Potassium (K) in mg",
  "Iron in mg",
  "Zinc in mg",
  "Vitamin B6 in mg",
  "Vitamin B12 in ug",
];

export default {
  data() {
    return {
      htmlTitle: "MyPlan",
      fc: [], // food card
      bc: [], // book card
      ec: [], // exc card
      gc: [], // game card
      mc: [], // music card
      nut: [],
      arrbar: [
        "Protein",
        "Vitamin A",
        "Vitamin C",
        "Calcium",
        "Folate",
        "Potassium",
        "Iron",
        "Zinc",
        "Vitamin B6",
        "Vitamain B12",
      ],
      colors: [
        { color: "#f56c6c", percentage: 20 },
        { color: "#e6a23c", percentage: 40 },
        { color: "#5cb87a", percentage: 60 },
        { color: "#1989fa", percentage: 80 },
        { color: "#6f7ad3", percentage: 100 },
      ],
    };
  },
  created() {
    // food part for pdf
    let fooditem = this.$store.state.meals;

    if (fooditem && Object.keys(fooditem).length !== 0) {
      Object.keys(fooditem).forEach((item) => {
        let arr = fooditem[item].recommend
        arr.forEach(n => {
          this.fc.push(n)
        })
      });
    }
    // console.log(sum)
    // this.fc = sum
    // console.log(this.fc)
    // let fooditem = this.$store.state.meal[0];
    // this.nut = this.$store.state.meal[1];
    // if (fooditem && Object.keys(fooditem).length !== 0) {
    //   Object.keys(fooditem).forEach((item) => {
    //     this.fc.push({
    //       name: item,
    //       quantity: fooditem[item]["quantity"],
    //       object: fooditem[item],
    //     });
    //   });
    // }
    // book part for pdf
    // let bookitem = this.$store.state.books;
    // if (bookitem && Object.keys(bookitem).length !== 0) {
    //   Object.keys(bookitem).forEach((item) => {
    //     this.bc.push({
    //       name: item,
    //       image: bookitem[item].image,
    //       author: bookitem[item].author.join(", "),
    //     });
    //   });
    // }
    // excirese part for pdf
    let excitem = this.$store.state.exc;
    if (excitem && Object.keys(excitem).length !== 0) {
      Object.keys(excitem).forEach((item) => {
        this.ec.push({
          name: item,
          des: excitem[item].Description,
          avoid: excitem[item]["Exercise Precautions"],
        });
      });
    }
    // game part for pdf
    // let gameitem = this.$store.state.games;
    // if (gameitem && Object.keys(gameitem).length !== 0) {
    //   Object.keys(gameitem).forEach((item) => {
    //     this.gc.push({
    //       name: item,
    //       cost: gameitem[item]["Cost in AUD"],
    //       cate: gameitem[item]["Category"],
    //       image: gameitem[item]["Image URL"],
    //     });
    //   });
    // }
  },
  methods: {
    download() {
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      downloadPDF(this.$refs.pdfplan, "MyPlan");
    },
    deletemeal(item) {
      this.fc.splice(this.fc.indexOf(item), 1);
      delete this.$store.state.meal[0][item.name];
      for (let i = 0; i < map.length; i++) {
        this.nut[i]["Your Energy Count"] =
          parseInt(this.nut[i]["Your Energy Count"]) -
          item.quantity * parseInt(item.object[map[i]]);
      }
      this.open1();
    },
    // deletegame(item) {
    //   this.gc.splice(this.gc.indexOf(item), 1);
    //   delete this.$store.state.games[item.name];
    //   this.open1();
    // },
    // deletebook(item) {
    //   this.bc.splice(this.bc.indexOf(item), 1);
    //   delete this.$store.state.books[item.name];
    //   this.open1();
    // },
    open1() {
      this.$notify({
        title: "Success",
        message: "Delete Successfully",
        type: "success",
        offset: 200,
      });
    },
  },
};
</script>
